import React from "react";
import "./App.css";
import GoogleApiWrapper from "./wrapper";



class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {cords:{
      lat: 47.444, lng: -122.176
    }};
    this.position=this.position.bind(this)

  }
  componentDidMount() {
    this.position()

    
    setInterval(this.position, 1000);
  }
   position =()=>{
    fetch("https://api.open-notify.org/iss-now.json",   {header: {
      'Access-Control-Allow-Origin':'*'}}
  )
    .then((response) => response.json())
    .then((data) => {
      
        this.setState({cords: {lat:data.iss_position.latitude, lng:data.iss_position.longitude}})
        console.log(this.state)
    });
  }
  render() {

  
  

    return (
      <div className="App">
        <h1 className='title'>Live <a target="_blank" rel="noopener noreferrer"  href='https://www.nasa.gov/mission_pages/station/main/index.html'>ISS</a> Position</h1>
        <GoogleApiWrapper cords={this.state.cords}/>

      </div>
    );
  }
}

export default App;

